import { Sampler } from "tone";
import { DetailChord } from "../redux/store/scoreData";

export const getDefaultSampler = () => {
    return new Sampler({
        "C4": "https://www.dropbox.com/s/9fgqyu7hsj8fncc/KEPSREC060.mp3?dl=0"
    }, {
        
      onload: () => console.log("done") // never gets called
    }).toDestination();
}

export const playChord = (sampler: Sampler, detail: DetailChord) => {
    
    sampler.triggerAttack('C3', '+0', 0.3);
    setTimeout(() => {
        sampler.triggerRelease('C4');
    }, 1000);
}