import styled from "styled-components";
import { DetailFixed } from "../../../../../redux/store/scoreData";

const ElementDetailFixed = (props: {
    detail: DetailFixed;
}) => {

    return (
        <_Wrap>
            <_Label>
                {props.detail.fixed}
            </_Label>
        </_Wrap>
    );
}

export default ElementDetailFixed;

const _Wrap = styled.div`
    display: inline-block;
    position: relative;
    width: 80px;
    height: 100%;
    border-radius: 30px 0 0 0;
    background-color: #817d9231;
    border: 1px solid #686d7c83;
`;

const _Label = styled.div`
    display: inline-block;
    width: 100%;
    height: 48px;
    margin-top: 30px;
    /* background-color: #ffffff65; */
    font-size: 28px;
    font-weight: 600;
    color: #f3f2f2;
    text-align: center;
`;
