import { useContext } from "react";
import styled from "styled-components";
import { DetailSection } from "../../../../../redux/store/scoreData";
import { GlobalContext } from "../../../../entry/systemEntry";

const ElementDetailSection = (props: {
    index: number;
}) => {
    const { store, dispatcher } = useContext(GlobalContext);

    const state = store.scoreState;
    const sectionNo = state.elementCacheList[props.index].sectionNo;
    const curSection = state.sectionCacheList[sectionNo];

    const barSum = Math.floor(curSection.beatSum / 4);
    const barRem = curSection.beatSum % 4;
    return (
        <_Wrap>
            <_Label>{curSection.labelName}</_Label>
            <_Len>{barSum}{barRem === 0 ? '' : ` (${barRem})`}</_Len>
            <_Border />
        </_Wrap>
    );
}

export default ElementDetailSection;

const _Wrap = styled.div`
    display: inline-block;
    height: 100%;
    position: relative;
    /* width: 100px; */
    text-align: center;
    border: 1px;
    /* border: 1px solid #c5c5cc; */
`;

const _Label = styled.div`
    display: block;
    position: relative;
    min-width: 80px;
    height: 20px;
    font-size: 18px;
    font-weight: 600;
    color: #c00;
    text-align: center;
    line-height: 20px;
    /* background-color: #1000eb42; */
`;
const _Len = styled.div`
    display: block;
    position: relative;
    /* min-width: 80px; */
    height: 16px;
    font-size: 14px;
    font-weight: 600;
    color: #242424;
    text-align: center;
    line-height: 16px;
    /* background-color: #00000042; */
`;

const _Border = styled.div`
    display: inline-block;
    position: relative;
    width: 4px;
    height: calc(100% - 34px);
    background-color: #c00;
`;
